html {
  scroll-behavior: smooth;
}

.App {

  text-align: center;
  background-color: #282c34;
 /* font-family: 'Yusei Magic', sans-serif;*/
 font-family: 'Roboto Condensed', sans-serif;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  /*background-color: #282c34;*/
  min-height: 60vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
.floaters{
  float: left;
}
.contactmeDiv{
  /*float: left;*/
  
  position: relative;
  color: #000000;
  margin-top: 20rem;
  padding-right: 1em;
  margin-left: 2rem;
}
.TextBlock{
  padding-left: 2rem;
  font-size: calc(10px + 2vmin);
  margin-left: 4rem;
  margin-right: 4rem;
  margin-top: 10rem;
  /*fontsize: "2vmin"*/
}
.icons{
  width: 2rem;
  padding-right: 4px;
}
.headerBlur{ 
  z-index:0;
  width: 99%;
  height: 70%;
  position: absolute;
  padding-top: 1px;
  
  background-image: url("images/background.png");
  background-position: center;
  background-size: cover; 
  opacity: 0.6;
  filter: blur(5px);
  -webkit-filter: blur(5px);
  -moz-filter: blur(5px);
  -o-filter: blur(5px);
 
 /* background-color: #ffffff;*/
}
.secondCont{ 
  margin-top: 5rem;
 /* background-color: #9b0606; debugv√§ri*/
 background-color: #282c34;
}
@media only screen and (max-width: 768px) {
.headerBlur{height: 60%;}
}
@media only screen and (max-width: 1000px) {
  .headerBlur{height: 60%;}
  }

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
